import React, {FC} from 'react';
import styles from './RangeInput.module.scss';
export interface Props {
    id?: string,
    min?: number,
    max?: number,
    value?: number,
    step?: number,
    name?: string,
    title?: string,
    disabled?: boolean
    onChange?: React.ChangeEventHandler<HTMLInputElement>
}
const RangeInput: FC<Props> = ({ onChange, id, name, min, max, value, step, disabled, title}) => {
    return (
        <input
            className={styles['range-input']}
            type="range"
            id={id}
            name={name}
            min={min}
            max={max}
            step={step}
            value={value}
            disabled={disabled}
            title={title}
            onChange={onChange}
        />
    );
};

export default RangeInput;
